import React from 'react';
import { platform, IOS } from '@vkontakte/vkui';
import * as moment from 'moment';

import PanelSpinner from "@vkontakte/vkui/dist/components/PanelSpinner/PanelSpinner";
import './msgbox.css';
const osName = platform();

class MsgBox extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            id: props.data.id,
            author: props.data.author,
            text: props.data.text,
            time: props.data.ts,
            owner: props.owner
        };
        this.align = 'left';
        this.color = 'dark';
    }

    componentDidMount() {
        //console.log(this.props);
        //console.log(this.state);
        this.align = (this.state.author === this.state.owner) ? 'right' : 'left';
        this.color = (['space_gray', 'dark_client'].includes(document.body.attributes.scheme.value)) ? 'dark' : 'light';
    }

    render() {


        return (
            <div className='row' >
                <div className={`block ${this.align} ${this.color}`}>
                    <span className='author'>{this.state.author}</span><div className='text'>{this.state.text}</div><span className='time'>{moment.unix(this.state.time).format('HH:mm')}</span>
                </div>
            </div>
        );
    }
}

export default MsgBox;
